<template>
  <div class="button">
    <a-button type="primary" @click="showModal">
      
    </a-button>
    <a-modal
        title=""
        :visible="visible"
        :confirm-loading="confirmLoading"
        @ok="handleOk"
        @cancel="handleCancel"
    >
      <a-form :form="form">
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label=""
        >
          <a-input
              v-decorator="[
          '',
          { rules: [{ required: checkNick, message: '' }] },
        ]"
              placeholder="Please input your nickname"
              v-model="key"
          />
        </a-form-item>
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label=""
        >
          <a-input
              v-decorator="[
          '',
          { rules: [{ required: checkNick, message: '' }] },
        ]"
              placeholder="Please input your nickname"
              v-model="workStyle"
          />
        </a-form-item>
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label=""
        >
          <a-date-picker style="width: 200px" v-model="beginDate"/>
        </a-form-item>
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label=""
        >
          <a-date-picker style="width: 200px" v-model="endDate" />
        </a-form-item>
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label=""
        >
          <a-input
              v-decorator="[
          '()',
          { rules: [{ required: checkNick, message: '()' }] },
        ]"
              placeholder="Please input your nickname"
              v-model="workPlace"
          />
        </a-form-item>
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label=""
        >
          <a-input
              v-decorator="[
          '',
          { rules: [{ required: checkNick, message: '' }] },
        ]"
              placeholder="Please input your nickname"
              v-model="major"
          />
        </a-form-item>
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label=""
        >
          <a-input
              v-decorator="[
          '',
          { rules: [{ required: checkNick, message: '' }] },
        ]"
              placeholder="Please input your nickname"
              v-model="workName"
          />
        </a-form-item>
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label=""
        >
          <a-input
              v-decorator="[
          '',
          { rules: [{ required: checkNick, message: '' }] },
        ]"
              placeholder="Please input your nickname"
              v-model="praise"
          />
        </a-form-item>
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label=""
        >
          <a-input
              v-decorator="[
          '',
          { rules: [{ required: checkNick, message: '' }] },
        ]"
              placeholder="Please input your nickname"
              v-model="prove"
          />
        </a-form-item>
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label=""
        >
          <a-input
              v-decorator="[
          '',
          { rules: [{ required: checkNick, message: '' }] },
        ]"
              placeholder="Please input your nickname"
              v-model="tel"
          />
        </a-form-item>
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label=""
        >
          <a-input
              v-decorator="[
          '',
          { rules: [{ required: checkNick, message: '' }] },
        ]"
              placeholder="Please input your nickname"
          />
        </a-form-item>
      </a-form>
    </a-modal>
  </div>
</template>

<script>
import bus from "./eventBus.js"

const formItemLayout = {
  labelCol: { span: 4 },
  wrapperCol: { span: 8 },
};
const formTailLayout = {
  labelCol: { span: 4 },
  wrapperCol: { span: 8, offset: 4 },
};
export default {
  data() {
    return {
      workStyle:'',
      beginDate: '',
      endDate: '',
      workPlace:'',
      major:'',
      workName:'',
      praise:'',
      prove:'',
      tel:'',
      ModalText: 'Content of the modal',
      visible: false,
      confirmLoading: false,
      formItemLayout,
      formTailLayout,
      form: this.$form.createForm(this, { name: 'dynamic_rule' }),
    };
  },

  methods: {
    showModal() {
      this.visible = true;
    },
    handleOk() {
      this.ModalText = 'The modal will be closed after two seconds';
      this.confirmLoading = true;
      setTimeout(() => {
        this.visible = false;
        this.confirmLoading = false;
      }, 2000);
      bus.$emit('workChange',this.key,this.workStyle,this.beginDate,this.endDate,this.workPlace,this.major,this.workName,this.praise,this.prove,this.tel,);
    },
    handleCancel() {
      console.log('Clicked cancel button');
      this.visible = false;
    },
  },
  check() {
    this.form.validateFields(err => {
      if (!err) {
        console.info('success');
      }
    });
  },
  handleChange(e) {
    this.checkNick = e.target.checked;
    this.$nextTick(() => {
      this.form.validateFields(['nickname'], { force: true });
    });
  },
}
</script>
<style>
.button {
  position: relative;
  left: 50px;
  float: left;
}
</style>