<template>
  <div class="button">
  <a-button @click="countDown">
    
  </a-button>
  </div>
</template>
<script>
export default {
  methods: {
    countDown() {
      let secondsToGo = 5;
      const modal = this.$success({
        title: '',
        content: ` ${secondsToGo}`,
      });
      const interval = setInterval(() => {
        secondsToGo -= 1;
        modal.update({
          content: ` ${secondsToGo}`,
        });
      }, 1000);
      setTimeout(() => {
        clearInterval(interval);
        modal.destroy();
      }, secondsToGo * 1000);
    },
  },
};
</script>
<style>
.button{
  position: relative;
  left: 50px;
  float: left;
}
</style>