<template>
  <div id="myChart" :style="{width: '300px', height: '300px'}"></div>
</template>

<script>
// 
let echarts = require('echarts/lib/echarts')
// 
require('echarts/lib/chart/pie')
// title
require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')
export default {
  name: 'hello',
  data() {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted() {
    this.drawLine();
  },
  methods: {
    drawLine() {
      // domecharts
      let myChart = echarts.init(document.getElementById('myChart'))
      // 
      myChart.setOption({
        title: {
          text: ''
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}: {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          left: 10,
          data: ['', '']
        },
        series: [
          {
            name: '',
            type: 'pie',
            radius: ['50%', '70%'],
            avoidLabelOverlap: false,
            label: {
              show: false,
              position: 'center'
            },
            emphasis: {
              label: {
                show: true,
                fontSize: '30',
                fontWeight: 'bold'
              }
            },
            labelLine: {
              show: true,
            },
            data: [
              {value: 300, name: ''},
              {value: 321, name: ''},
            ]
          }
        ]
      });
    }
  }
}
</script>

